
.project {
    padding: 2em;
    margin: 0 auto;
    text-align: center;
    box-shadow: var(--shadow);
    transition: transform 0.2s linear;
    height: 300px; /* set the height of the project container */
    width: 300px;
    background-size: cover; /* scale the background image to cover the entire container */
    background-position: center; /* center the background image within the container */
    background-repeat: no-repeat; /* prevent the background image from repeating */
  /* add any additional styles you want */

  }

  .project:hover {
    transform: translateY(-7px);
    
  }
  
  .project__description {
    margin-top: 1em;
  }
  
  .project__stack {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 1.2em 0;
    
  }
  
  .project__stack-item {
    margin: 0.5em;
    font-weight: 500;
    font-size: 0.8rem;
    color: var(--clr-fg-alt);
  }
  
  .project .link--icon {
    margin-left: 0.5em;
  }